$breakpoints: (
  sm: (
    min-width: 576px,
  ),
  md: (
    min-width: 768px,
  ),
  lg: (
    min-width: 992px,
  ),
  xl: (
    min-width: 1200px,
  ),
  xxl: (
    min-width: 1400px,
  ),
);

$orientations: (
  l: (
    orientation: landscape,
  ),
  p: (
    orientation: portrait,
  ),
);

$font-family: "Inter", sans-serif;

$font-size-xl: 24px;
$font-size-lg: 16px;
$font-size-md: 14px;
$font-size-sm: 12px;

$font-weight-bold: 700;
$font-weight-semi-bold: 600;
$font-weight-regular: 400;

$line-height-xl: 36px;
$line-height-lg: 24px;
$line-height-md: 20px;
$line-height-sm: 16px;

$primary: #7860d7;
$secondary: #05c9b7;

$body-bg: #221b3a;
$body-color: #fff;

$footer-color: #8d7fbc;

$red: #e34a67;
$violet: #7860d7;
$turquoise: #00c9b7;

$ring-gradient: linear-gradient(180deg, #2b2347 0%, #221b3a 100%);

$tile-gradient: linear-gradient(360deg, #2b224a 0%, rgba(43, 34, 74, 0) 100%);
$tile-hover-gradient: linear-gradient(
  0deg,
  rgba(43, 34, 74, 0.92) 7.69%,
  rgba(70, 58, 113, 0.92) 100%
);

$transition-duration: 0.3s;

$scrollbar-width: 4px;

@mixin media($breakpoint, $orientation: null) {
  $size: map-get($breakpoints, $breakpoint);
  $viewport: map-get($orientations, $orientation);
  @if ($size and $viewport) {
    @media #{$size} and #{$viewport} {
      @content;
    }
  } @else if ($size) {
    @media #{$size} {
      @content;
    }
  } @else {
    @error 'Wrong breakpoint or orientation value';
  }
}

@mixin footer-font {
  font-family: $font-family;
  font-size: $font-size-sm;
  font-weight: $font-weight-regular;
  color: $footer-color;
}

@keyframes close {
  to {
    transform: rotate(270deg) scale(0);
  }
}

@keyframes show-modal {
  from {
    transform: scale(0) translate(-50%, -50%);
  }
  to {
    transform: scale(1) translate(-50%, -50%);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes up {
  from {
    top: 100%;
  }
  to {
    top: 0;
  }
}

@keyframes down {
  from {
    bottom: 100%;
  }
  to {
    bottom: 0;
  }
}

@keyframes fly {
  @for $i from 0 through 19 {
    #{$i * 1%} {
      transform: rotate($i * 0.5deg);
    }
  }
  20% {
    top: -1000px;
    transform: rotate(10deg);
  }
  30% {
    top: -1000px;
  }
}

@keyframes smoke {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(0.7);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes shake {
  @for $i from 1 through 99 {
    $shaking: 1px;
    @if ($i % 2 == 0) {
      $shaking: -1px;
    }
    @if ($i % 3 == 0) {
      $shaking: 2px;
    }
    @if ($i % 4 == 0) {
      $shaking: -2px;
    }
    #{$i * 1%} {
      transform: translate($shaking, $shaking);
    }
  }
}

@for $i from 0 through 4 {
  $l: 100% / 6;
  @keyframes zoom-in-out-#{$i + 1} {
    0% {
      transform: scale(0);
    }
    #{$i * $l} {
      transform: scale(0);
    }
    #{$i * $l + $l} {
      transform: scale(1.2);
    }
    #{$i * $l + $l * 2} {
      transform: scale(0);
    }
    100% {
      transform: scale(0);
    }
  }
}

.button {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 12px 32px;
  background: $primary;
  border-radius: 8px;
  font-family: $font-family;
  font-weight: $font-weight-semi-bold;
  font-size: $font-size-lg;
  line-height: $line-height-lg;
  color: $body-color;
  cursor: pointer;
  transition: $transition-duration;
  z-index: 1;

  &:hover::after {
    opacity: 1;
    top: 0;
    bottom: 0;
    height: 100%;
  }

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 50%;
    top: 50%;
    bottom: -50%;
    left: 0;
    border-radius: 8px;
    background-color: $secondary;
    opacity: 0;
    transition: 0.3s;
    z-index: -1;
  }
}

