.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform-origin: 0 0;
  animation: show-modal 0.3s forwards;
  z-index: 6;
  width: 95%;
  max-width: 540px;
  height: 95%;
  display: flex;
  flex-direction: column;
  padding: 20px 24px 24px;
  background: linear-gradient(0deg, #2b224a, #2b224a), #ffffff;
  border: 1px solid #342957;
  box-shadow: 0px 12px 32px rgba(22, 13, 49, 0.4);
  border-radius: 16px;

  @include media(md) {
    width: 540px;
    height: 360px;
  }

  .modal-headline {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    .modal-title {
      font-family: $font-family;
      font-weight: 700;
      font-size: 20px;
      line-height: 32px;
      text-transform: capitalize;
    }

    .modal-close {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      cursor: pointer;

      &--clicked {
        animation: close 0.5s linear forwards;
      }

      svg {
        fill: #c3b8e8;
      }
    }
  }

  .details {
    overflow: hidden;
    width: 100%;
    height: 100%;
    ::-webkit-scrollbar {
      width: 4px;
    }

    ::-webkit-scrollbar-track {
      background-color: #221a3c;
      border-radius: 8px;
    }

    ::-webkit-scrollbar-thumb {
      background-color: #53477d;
      border-radius: 8px;
    }

    &::after {
      position: absolute;
      height: 52px;
      left: 24px;
      right: 28px;
      bottom: 24px;
      content: "";
      background: linear-gradient(180deg, rgba(43, 34, 74, 0) 0%, #2b224a 100%);
    }

    .details-header {
      display: flex;
      height: 40px;
      width: 100%;
      margin-bottom: 16px;
      background-color: #342a56;
      border-radius: 4px;
      color: #a094c6;
      font-weight: $font-weight-semi-bold;
      font-size: $font-size-md;
      line-height: $line-height-md;
      text-transform: capitalize;

      .details-type {
        display: flex;
        padding: 10px 16px;
        align-items: center;
        justify-content: space-between;
        background-color: #251d42;
        width: 50%;
        border-radius: 4px;
        overflow: hidden;

        .details-sort {
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 16px;
          height: 16px;
          cursor: pointer;
          overflow: hidden;
          flex-shrink: 0;

          svg {
            fill: #a094c6;
          }

          .arrow-up {
            position: absolute;
            animation: up 0.3s;
          }

          .arrow-down {
            position: absolute;
            animation: down 0.3s;
          }
        }
      }

      .details-heading {
        width: 194px;
      }

      .details-status {
        display: flex;
        padding: 10px 16px;
        align-items: center;
        width: 50%;
      }
    }

    .details-list {
      position: relative;
      display: flex;
      flex-direction: column;
      display: flex;
      max-width: 492px - $scrollbar-width / 2;
      height: 90%;
      overflow-y: auto;
      overflow-x: hidden;
      scrollbar-color: #53477d #221a3c;
      scrollbar-width: thin;

      @-moz-document url-prefix() {
        width: 492px;
      }

      @include media(md) {
        height: 212px;
      }

      .details-loading {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 24px;
        height: 24px;
        transform: translate(-50%, -50%);
        svg {
          fill: #d2cbe9;
          animation: spin 1s linear infinite;
        }
      }

      .details-error {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: $font-family;
        font-size: $font-size-md;
        line-height: $line-height-md;
        font-weight: $font-weight-regular;
        text-align: center;
        color: #d2cbe9;
      }

      .details-item {
        position: relative;
        display: flex;
        margin-bottom: 28px;
        width: 100%;
        min-height: $line-height-md;
        font-family: $font-family;
        font-size: $font-size-md;
        line-height: $line-height-md;
        font-weight: $font-weight-regular;
        overflow: hidden;
        color: #d2cbe9;

        .item-left {
          padding: 0 16px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 50%;
        }

        .item-right {
          padding: 0 16px;
          left: calc(50% + #{$scrollbar-width});
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 50%;
        }
      }
    }
  }
}
